    background: 0 0;
	margin: 30rpx auto;
}
.mip-tabs-label-wrap {
    font-size: 14px;
    line-height: 1.5;
    position: relative;
    background: #fff;
    z-index: 2;
}
.mip-tabs-label-wrap>view {
    overflow-x: auto;
    white-space: nowrap;
    position: relative;
}
.mip-tabs-label-wrap .optionColor, .mip-tabs-label-wrap .optionColor span {
    background: #fccd11;
    color: #303133;
    border-radius: 30px;
}
.mip-tabs-label-wrap .mip-tabs-label {
    padding: 0 5px;
    height: 30px;
    line-height: 30px;
    margin-right: 10px;
}
.mip-tabs-label-wrap .mip-tabs-label {
    padding: 0 20px;
    height: 40px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 40px;
    display: inline-block;
    list-style: none;
    font-size: 14px;
